body {
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.menu_list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
}

.header_menu {
  display: flex;
  padding: 0 50px;
  justify-content: space-between;
  align-items: center;
}

.menu_logo {
  align-self: center;
  justify-content: center;
  height: 40px;
}

.nav_item:not(:first-child) {
  margin-left: 20px;
}

.menu_link {
  text-decoration: none;
  font-size: 12px;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
  display: flex;
  font-weight: 500;
  align-items: center;
  color: black;
  height: 60px;
}

.menu_link:hover,
.menu_link:active,
.is-active {
  color: #00acdc;
}

.is-active {
  position: relative;
}

.is-active::after {
  content: "";
  position: absolute;
  border-radius: 8px;
  background-color: #00acdc;
  height: 4px;
  width: 100%;
  bottom: 0;
  left: 0;
}
